.home-content {
  min-height: 650px;
  width: 100%;
  color: #fff;
  font-family: 'Raleway', sans-serif;
  margin-left: 0px;
}

.home-content .overlay {
  min-height: 650px;
  background: #16a085;
  opacity: 0.9;
  margin-left: -2rem;
  padding: 0;
}

.home-content .overlay hr {
  margin-left: 0px;
  margin-right: 0px;
}

.home-content .overlay .name {
  font-size: 35px;
  padding: 1.5rem;
  margin-left: 1.5rem;
  text-transform: uppercase;
}

.home-content .overlay .headline, .home-content .overlay .tagline, .home-content .overlay .description {
  margin-bottom: 2.5rem;
  padding: 1.5rem;
}

.home-content .overlay .headline {
  font-size: 28px;
  margin-top: -10px;
}

.home-content .overlay .contact {
  bottom: 40px;
  padding: 3rem;
}

.home-content .overlay .contact .contact-me {
  font-weight: 800;
}

.home-content .headline {
  font-size: 50px;
  letter-spacing: 0px;
  margin: 2rem;
}

.home-content .tagline {
  font-size: 25px;
  margin: 2rem;
}

.home-content .see-more {
  margin-top: 10rem;
}

.home-content .see-more-icon > .fa {
  margin-top: 1rem;
  animation-timing-function: ease-in-out;
  animation: see-more-arrow 2s infinite;
}

.home-content .description {
  font-size: 18px;
}

#features {
  min-height: 650px;
  background: #eee;
  padding: 5rem;
  color: #000;
  font-family: 'Raleway', sans-serif;
}

#features .feature {
  padding: 5rem;
}

#features .feature .caption {
  font-weight: bold;
}

#features .feature img {
  border: 3px solid #fff;
}

#contact {
  min-height: 650px;
  width: 100%;
  padding: 1.5rem;
}

#about {
  min-height: 650px;
  width: 100%;
  color: #000;
  padding: 5rem;
}

#about .about-container {
  padding: 2rem;
  border: 1.5px solid #eee;
  border-radius: 5px 5px 5px 5px;
}

#about .about-container .image > img {
  height: 400px;
  width: 400px;
  border: 2px solid #eee;
}

.app_header {
  height: 70px;
  width: 100%;
  margin-left: 0px;
  margin-right: 0px;
  background: #000;
  color: #fff;
  display: none;
}

.app_header .brand {
  padding-top: 1rem;
}

.app_header .brand .navbar-brand {
  padding-top: -1.5rem;
  font-family: 'Raleway', sans-serif;
}

.app_header .nav-wrapper .menu {
  float: right;
  padding-top: 2.5rem;
}

.app_header .nav-wrapper .menu ul {
  list-style-type: none;
}

.app_header .nav-wrapper .menu ul .page_item {
  display: inline;
  margin-left: 1rem;
}

.app_header .nav-wrapper .menu ul .page_item a {
  color: #fff;
  font-family: 'Raleway', sans-serif;
}

.app_footer {
  min-height: 40px;
  background: #000;
  color: #fff;
  padding-top: 1rem;
  font-size: 12px;
  margin: 0px auto;
  width: 100%;
}

.app_footer .social-media .facebook {
  color: #3b5998;
}

.app_footer .social-media .facebook a {
  color: #3b5998;
}

.app_footer .social-media .instagram > a {
  background: -webkit-linear-gradient(left, #405de6, #5851db, #833ab4, #c13584, #e1306c, #fd1d1d, #f56040, #f77737, #fcaf45, #ffdc80);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.app_footer .social-media .github {
  color: #f5f5f5;
}

.app_footer .social-media .github a {
  color: #f5f5f5;
}

.app_footer .social-media .twitter {
  color: #1da1f2;
}

.app_footer .social-media .twitter a {
  color: #1da1f2;
}
