.home-content{
	min-height: 650px;
	width: 100%;
	
	color: #fff;
	font-family: 'Raleway', sans-serif;
	margin-left: 0px;
	
	.overlay{
		min-height: 650px;
		background: #16a085;
		opacity: 0.9;
		margin-left:-2rem;
		padding: 0;
		
		hr{
			margin-left:0px;
			margin-right:0px;
		}
		
		.name{
			font-size: 35px;
			padding: 1.5rem;
			margin-left: 1.5rem;
			text-transform: uppercase;
		}
		
		.headline, .tagline, .description{
			margin-bottom: 2.5rem;
			padding: 1.5rem;
		}

		.headline{
			font-size:28px;
			margin-top:-10px;
		}

		.contact{
			bottom:40px;
			padding: 3rem;
			
			.contact-me{
				font-weight:800;
			}
		}
	}
	
	.headline{
		font-size: 50px;
		letter-spacing: 0px;
		margin: 2rem;
	}

	.tagline{
		font-size: 25px;
		margin: 2rem;
	}

	.see-more{
		margin-top: 10rem;
	}

	.see-more-icon > .fa{
		margin-top:1rem;
		animation-timing-function: ease-in-out;
		animation: see-more-arrow 2s infinite;
	}

	.description{
		font-size:18px;
	}
}